.experience_container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    margin-top: 2rem;
}


.experience_container > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience_container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience_container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.experience_details{
    display: flex;
    gap: 1rem;
}

.experience_details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}

/* =========================== MEDIUM DEVICES ========================== */

@media screen and (max-width:1024px) {
    #experience{
        margin-top: 11rem;
    } 
    .experience_container{
        margin-top: 1rem;
        grid-template-columns: 1fr;
    }

    .experience_container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience_content{
        padding: 1rem;
    }
 }
 
 /* =========================== SMALL DEVICES ========================== */
   
   @media screen and (max-width:600px) {
      #experience{
          margin-top: 12rem;
      } 
      .experience_container{
          gap: 1rem;
          margin-right: 3.6rem;
          width: 80%;
      }

      .experience_container > div{
          width: 100%;
          padding: 2rem 1rem;
      }
   }
 
 /* Change Theme */

.experience-container-theme{
    display: grid;
}

.experience-container-theme > div{
    background: rgb(31, 86, 187);
    
}

.experience-container-theme > div:hover{
    background: var(--color-bg-variant);
    border-color: var(--color-primary-variant);
    cursor: default;
}
